import Vue from 'vue';
import Router from 'vue-router';

import MainDashbord from './views/MainDashbord.vue';
import UserProfileLite from './views/UserProfileLite.vue';
import Errors from './views/Errors.vue';
import Review from './views/Review.vue';
import ReviewEditor from './views/ReviewEditor.vue';
import Product from './views/Product.vue';
import ProductDetail from './views/ProductDetail.vue';
import ArticleList from './views/Article.vue';
import ArticleEditor from './views/ArticleEditor.vue';
import PromotionList from './views/Promotion.vue';
import PromotionDetail from './views/PromotionDetail.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  linkActiveClass: 'active',
  linkExactActiveClass: 'exact-active',
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
  routes: [
    {
      path: '/',
      redirect: '/dashbord',
    },
    {
      path: '/dashbord',
      name: 'dashbord',
      component: MainDashbord,
    },
    {
      path: '/user-profile-lite',
      name: 'user-profile-lite',
      component: UserProfileLite,
    },
    {
      path: '/errors',
      name: 'errors',
      component: Errors,
    },
    {
      path: '/review',
      name: 'review',
      component: Review,
    },
    {
      path: '/review-editor',
      name: 'review-editor',
      component: ReviewEditor,
    },
    {
      path: '/product-list',
      name: 'product-list',
      component: Product,
    },
    {
      path: '/product-detail',
      name: 'product-detail',
      component: ProductDetail,
    },
    {
      path: '/article-list',
      name: 'article-list',
      component: ArticleList,
    },
    {
      path: '/article-editor',
      name: 'article-editor',
      component: ArticleEditor,
    },
    {
      path: '/promotion-list',
      name: 'promotion-list',
      component: PromotionList,
    },
    {
      path: '/promotion-detail',
      name: 'promotion-detail',
      component: PromotionDetail,
    },
    {
      path: '*',
      redirect: '/errors',
    },
  ],
});
