<script>
import SidebarActions from "@/components/add-new-post/SidebarActions.vue";
import SidebarCategories from "@/components/add-new-post/SidebarCategories.vue";

export default {
  data() {
    return {
      productName:'',
      isNewArrival:false,
      isBestSaller: false,
      status: 'InActive',
      categoryId:'',
      IsStatus: false,
      category:1,
      IsVisibility:false,
      content: "Create Some Content",
      coverImageUrl: 'https://pbs.twimg.com/media/CMgHSDrUwAAVexq.jpg',
      productImageUrl: [],
      editorOption: {},
      selected: null,
      options: [
          { value: null, text: 'Please select an option' },
          { value: 'first', text: 'This is the first option' },
          { value: 'second', text: 'This is the second option' },
          { value: 'disabled', text: 'This one is disabled', disabled: true }
      ]
    };
  },
  methods: {
    onEditorBlur(quill) {
      console.log("editor blur!", quill);
    },
    onEditorFocus(quill) {
      console.log("editor focus!", quill);
    },
    onEditorReady(quill) {
      console.log("editor ready!", quill);
    },
    onEditorChange({ quill, html, text }) {
      console.log("editor change!", quill, html, text);
      this.content = html;
    },
    onCoverImageChange(e) {
      const file = e.target.files[0];
      this.coverImageUrl = URL.createObjectURL(file);
    },
    onProductImageChange(e) {
      const file = e.target.files;
      for (let index = 0; index < file.length; index++) {
        this.productImageUrl.push({
          id:uuidv4(),
          file:file[index],
          url:URL.createObjectURL(file[index])
        });
      }
    },
    deleteImg(id) {
      this.productImageUrl = this.productImageUrl.filter(e=>e.id !== id);
    }
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill;
    }
  },
  components: {
    anpSidebarActions: SidebarActions,
    anpSidebarCategories: SidebarCategories
  }
};

function uuidv4() {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
    let r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
    return v.toString(16);
  });
}

</script>
